.kq {
    background: #1A8065;
    display: block;
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    color: #fff;
    z-index: 2;
    padding: 5px 0;
    @media #{$r768} {
        position: relative;
        left: auto;
        bottom: auto;
    }
    p {
        margin: 0;
    }
}

a {
    cursor: pointer;
}

.pulse_regist {
    box-shadow: 0 0 0 0 rgba(3, 129, 114, 0.7);
    -webkit-animation: won 1.25s infinite cubic-bezier(0.66, 0, 0, 1);
    -moz-animation: won 1.25s infinite cubic-bezier(0.66, 0, 0, 1);
    -ms-animation: won 1.25s infinite cubic-bezier(0.66, 0, 0, 1);
    animation: won 1.25s infinite cubic-bezier(0.66, 0, 0, 1);
}

@-webkit-keyframes won {
    to {
        box-shadow: 0 0 0 10px rgba(232, 76, 61, 0)
    }
}

@-moz-keyframes won {
    to {
        box-shadow: 0 0 0 10px rgba(232, 76, 61, 0)
    }
}

@-ms-keyframes won {
    to {
        box-shadow: 0 0 0 10px rgba(232, 76, 61, 0)
    }
}

@keyframes won {
    to {
        box-shadow: 0 0 0 10px rgba(232, 76, 61, 0)
    }
}
.fade-in {
    animation-name: fadeIn;
    animation-duration: 1.3s;
    animation-timing-function: cubic-bezier(0, 0, 0.4, 1);
    animation-fill-mode: forwards;
}
  
@keyframes fadeIn {
    from {
      opacity: 0;
    }
  
    to {
      opacity: 1;
    }
}
.modal_none{
    display: none;
}
.modal {
    &-header{
        padding: 0;
    }
    &-box{
        @media #{$r768}{
            width: 80%;
        }
    }
    &-title {
        color: #fff;
        text-align: center;
        font-size: 25px;
        background-color: #248a80;
        padding: 10px 10px;
        text-transform: uppercase;
        font-weight: 500;
    }
    &-close{
        color: #fff;
        margin-top: 13px;
        padding-right: 20px;
    }
    &-body {
        width: 100%;
        color: #000;
        .popup_map_2_0_0{
            &__item{
                display: flex;
                justify-content: space-between;
                align-items: center;
                margin:0px auto 20px;
                width: 100%;
                border-bottom: 1px dashed #dddddd;
                padding-bottom: 8px;
                &:last-child{
                    border-bottom: 0;
                    padding-bottom: 0;
                }
                i{
                    font-size: 12px;
                }
            }
            &__add{
                width: 65%;
            }
            &__call{
                width: 10%;
                a{
                    background-color: #ef810b;
                    padding: 3px 15px;
                    border-radius: 4px;
                    position: relative;
                    &::before{
                        content: "";
                        position: absolute;
                        top: 4px;
                        left: 6px;
                        background-image: url(../images/call.svg);
                        background-position: center;
                        background-repeat: no-repeat;
                        width: 18px;
                        height: 18px;
                        background-size: 100%;
                    }
                }
                i{
                    color: #fff;
                }
            }
            &__regist{
                width: 25%;
                color: #fff;
                a{
                    background-color: #ef810b;
                    color: #fff;
                    padding: 3px 30px;
                    text-transform: uppercase;
                    border-radius: 4px;
                }
            }
        }
    }
}
#modal-call{
    .modal-header{
        border-bottom: 1px solid #fff;
    }
    .modal-title{
        background-color: unset;
    }
    .modal-close{
        color: #000;
    }
    .modal-body{
        text-align: center;
        .call_ct{
            padding: 0 35px;
            display: none;
        }
        .call_sub{
            padding: 20px 0 10px;
            color: #666;
            font-weight: 600;
            font-style: italic;
        }
        .pop-call{
            background: #dcaf47;
            color: #fff;
            text-transform: uppercase;
            display: block;
            padding: 12px 0;
            border-radius: 4px;
            font-weight: bold;
            text-transform: uppercase;
            text-decoration: none;
            margin: 0 auto;
        }
        .pop-call-icon {
            background: url(../images/pop-call.png) 0 0 no-repeat;
            width: 22px;
            height: 22px;
            display: inline-block;
            vertical-align: middle;
            margin-right: 10px;
        }
        .pop_fr {
            padding: 5px 35px 1px;
        }
        .pop-note {
            padding: 20px 0 10px;
            color: #666;
            font-weight: 600;
            font-style: italic;
        }
        .pop_fr>div:nth-child(1) input {
            width: 100%;
            margin-bottom: 4px;
            font-size: 14px;
            padding: 12px 0;
            text-align: center;
            color: #333;
            border-radius: 5px;
        }
        .pop_fr>div:nth-child(2) input {
            width: 100%;
            background: #1a8065;
            color: #fff;
            border: none;
            cursor: pointer;
            font-size: 14px;
            margin-top: 5px;
            border-radius: 4px;
            padding: 12px 0;
        }
    }
}

.temp {
    padding-top: 40px;
    padding-bottom: 40px;
    background-position: center 0;
    background-repeat: no-repeat;
    position: relative;
    background-size: 100%;
    @media #{$r1440} {
        background-size: 100%;
    }
    @media #{$r1280} {
        background-size: 105%;
    }
    @media #{$r1024} {
        background-size: 120%;
        padding-top: 30px;
        padding-bottom: 30px;
    }
    @media #{$r768} {
        background-size: 140%;
    }
    &.mbReg {
        padding: 0;
    }
    &Title {
        padding-bottom: 10px;
        position: relative;
        text-align: center;
        p {
            font-size: 50px;
            color: #026059;
            text-transform: uppercase;
            line-height: 1.3;
            @media #{$r1366}{
                font-size: 40px;
            }
            @media #{$r1024}{
                font-size: 30px;
            }
            b{
                font-weight: 700;
            }
            br{
                display:none;
            }
        }
        span{
            font-size: 20px;
            font-weight: 600;
            @media #{$r414}{
                font-size: 14px;
            }
        }
        &-white {
            color: #fff;
        }
        &-color {
            color: #004689;
        }
        img {
            max-width: 100%;
            @media #{$r1024}{
                max-width: 50%;
            }
        }
    }
    .line{
        width: 35%;
        margin: 20px auto;
        height: 2px;
        background-color: #646464;
        position: relative;
        &::after{
            content:'';
            position: absolute;
            top:-10px;
            left: 49%;
            background-image: url(../images/icon_logo.png);
            background-size: 100%;
            width: 22px;
            height: 22px;
        }
    }
    &Sub {
        text-align: center;
        font-size: 18px;
        text-transform: uppercase;
    }
    &Line {
        background-repeat: no-repeat;
        width: 258px;
        border-top: 1px solid #d8d8d8;
        position: relative;
        @media #{$r768} {
            margin: 20px auto 30px;
        }
        &::before {
            width: 100px;
            margin: 0;
            border-top: 3px solid #f69679;
            position: absolute;
            top: -2px;
            content: "";
            @media #{$r768} {
                left: 76px;
            }
        }
        &-left {
            margin: 10px 0 30px;
            border: none;
            &::before {
                width: 70px;
                left: 0;
            }
        }
        &-center {
            margin: 20px auto 30px;
            &::before {
                left: 80px;
            }
        }
    }
    &2,
    &3,
    &4 {
        position: relative;
        &:after {
            content: '';
            width: 100%;
            height: 100%;
            position: absolute;
            top: 0;
            left: 0;
            z-index: -1;
        }
        &.loaded:after {
            opacity: 0;
        }
    }
}

.regist {
    @extend %font1;
    font-size: 14px;
    &Note {
        margin-bottom: 5px;
        text-align: center;
        font-size: 20px;
        color: #333;
        @media #{$r768} {
            font-size: 12px;
        }
        &-white {
            color: #fff;
        }
        &-black {
            color: #000;
        }
        &-up {
            text-transform: uppercase;
        }
        span {
            display: block;
            font-weight: 600;
        }
    }
    &Button {
        background-image: linear-gradient(to bottom, #ee682a, #e3353d);
        color: #fff;
        text-transform: uppercase;
        padding: 10px 20px 10px 25px;
        display: table;
        font-weight: 600;
        margin: 10px auto 0;
        font-size: 20px;
        transition: all 0.5s;
        @media #{$r1440}{
            font-size: 17px;
        }
        &:hover{
            @extend .pulse_regist;
            border-radius: 30px;
            transition: all 0.5s;
        }
        @media #{$r768} {
            padding: 8px 8px 8px 13px;
            font-size: 12px;
        }
    }
    &Button2 {
        border: 1px solid #f3bb86;
        color: #f3bb86;
        border-radius: 8px;
        text-transform: uppercase;
        padding: 4px 20px 4px 25px;
        display: table;
        margin: 15px auto 0;
        @media #{$r768} {
            padding: 4px 8px 4px 13px;
            font-size: 11px;
        }
    }
    a {
        &:after {
            display: none;
            width: 0;
            overflow: hidden;
            transition: 0.3s;
            content: '»';
            display: inline-block;
            @extend %icon;
        }
        &:hover::after {
            width: 15px;
        }
    }
    &Note {
        &-white {
            color: #fff;
        }
        &-color {
            color: #e7b57a;
        }
    }
}
