.temp5 {
    padding-top: 40px;
    padding-bottom: 0;
    min-height: 880px;
    position: relative;
    &.loaded {
        background-image: url(../images/t5-bg.jpg?ver=1);
        @media #{$r1440}{
            background-size: 110%;
        }
        @media #{$r1280}{
            background-size: 118%;
        }
        @media #{$r1024}{
            background-size: 119%;
            min-height: 570px;
        }
    }
    .tempTitle{
        padding-top: 40px;
        @media #{$r1440}{
            padding-top: 10px;
        }
        img{
            @media #{$r1440}{
                width: 30%;
            }
        }
    }
    @media #{$r1440} {
        min-height: 530px;
    }
    @media #{$r1366} {
        min-height: 610px;
    }
    @media #{$r1024} {
        min-height: 690px;
    }
    @media #{$r768} {
        background-size: 920px;
        min-height: 600px;
        padding-top: 10px;
    }
    .tempTitle{
        p{
            line-height: 45px;
        }
        span{
            color: #0F8E89;
        }
        &::after{
            display: none;
        }
       
    }
    &__thumb{
        .thumbnails .tns-nav-active {
            background: none;
            border-color: transparent;
            img{
                border:2px solid #1A8065;
            }
        }
        .thumbnails img {
            max-height: 100px;
            @media #{$r1440}{
                max-height: 80px;
            }
           
        }
        .thumbnails li {
            width: 120px;
            border:none;
            @media #{$r1440}{
                width: 110px;
            }
        }
    }
    &__box{
        padding-top: 0;
        @media #{$r1440}{
            width: 80%;
            margin: 0 auto;
        }
        @media #{$r1280}{
            padding-top: 0px;
        }
        .tabs{
            position: absolute;
            bottom: 20px;
            left: 70px;
            z-index: 2;
            @media #{$r1440}{
                bottom: 20px;
                left: 170px;
            }
            @media #{$r1366}{
                bottom: 30px;
                left: 140px;
            }
            @media #{$r1024}{
                bottom: 0px;
                left: 90px;
            }
            @media #{$r768}{
                bottom: -100px;
                left: 70px;
            }
            .tab{
                margin: 0 10px;
                &:focus{
                    outline: unset;
                }
                img{
                    border-radius: 100%;
                    &.active{
                        border: 2px solid #1A8065;
                    }
                }
            }
        }
        .item{
            display: flex;
            flex-wrap: wrap;
            .text{
                text-align: center;
                width: 50%;
                padding-top: 60px;
                transition: all 0.3s;
                @media #{$r1440}{
                    padding-top: 30px;
                    padding-left: 30px;
                }
                @media #{$r1366}{
                    padding-top: 10px;
                    padding-left: 0px;
                }
                &:hover{
                    transition: all 0.3s;
                    cursor: pointer;
                    h2{
                        color: #ef810a;
                    }
                    p{
                        color: #147e7a;
                    }
                    ul{
                        li{
                            color: #147e7a;
                        }
                    }
                }
                h2{
                    color: #0F8E89;
                    font-size: 38px;
                    @media #{$r1440}{
                        font-size: 35px;
                        margin: 10px 0 0;
                    }
                    @media #{$r1024}{
                        font-size: 40px;
                    }
                    @media #{$r768}{
                        font-size: 30px;
                    }
                }
                p{
                    color: #333;
                    font-size: 20px;
                    text-transform: uppercase;
                    @media #{$r1440}{
                        font-size: 18px;
                    }
                    @media #{$r768}{
                        font-size: 15px;
                    }
                }
                ul{
                    padding-left: 0;
                    list-style: none;
                    border-bottom: 1px solid;
                    padding-bottom: 20px;
                    @media #{$r1024}{
                        padding-bottom: 10px;
                        font-size: 13px;
                    }
                    li{
                        color: #000;
                        @media #{$r768}{
                            font-size: 12px;
                        }
                    }
                }
            }
            .pic{
                width: 50%;
                img{
                    display: block;
                    width: 100%;
                    height: auto;
                }
            }
        }

    }
    .regist{
        p{
            font-size: 15px!important;
            text-transform: initial!important;
            color:#646464!important;

        }
    }
}

.tempReg {
