        }
        @media #{$r1366} {
            width: 80%;
        }
        @media #{$r1280} {
            margin-left: 105px;
        }
        @media #{$r1024} {
            width: 100%;
            margin: 0 0px 10px;
        }
        @media #{$r768} {
            width: 100%;
            margin: 0;
        }
        h3 {
            font-size: 24px;
            text-transform: uppercase;
            margin: 0;
            color: #f0c150;
            @media #{$r1366} {
                font-size: 22px;
            }
            @media #{$r1024} {
                font-size: 21px;
            }
            img {
                max-width: 100%;
                @media #{$r768} {
                    width: auto;
                }
            }
        }
