            color: #fff;
            @media #{$r1366} {
                margin: 0 0 20px;
            }
            span {
                text-transform: uppercase;
            }
        }
        hr {
            border-top: 1px solid #eee;
            margin: 10px auto;
            width: 90%;
        }
        &>div {
            padding: 0;
            margin: 0;
            color: #fff;
            @media #{$r1024} {
                padding: 0;
            }
        }
        article {
            margin: 0 auto;
            padding: 0;
            width: 100%;
        }
        .dkbt {
            padding: 10px 0 0;
            position: relative;
            .mxh {
                position: absolute;
                right: 50px;
                top: 15px;
                width: 80px;
                @media #{$r1024} {
                    right: 30px;
                }
            }
        }
        .fct input,
        .fct textarea,
        .fct select {
            width: 100%;
            margin: 4px 0 7px;
            border: none;
            border-radius: 4px;
            padding: 8px 10px;
            background: #fff;
            ;
            color: #000;
            outline: none;
            @media #{$r1366} {
                margin: 4px 0 2px;
            }
        }
        .fct textarea {
            height: 102px;
            resize: none;
            font-family: arial;
            font-size: 14px;
            @media #{$r1024} {
                height: 62px;
            }
        }
        .fct {
            input:focus::-webkit-input-placeholder {
                /* Chrome/Opera/Safari */
                color: #fff;
            }
            input:focus::-moz-placeholder {
                /* Firefox 19+ */
                color: #fff;
            }
            input:focus:-ms-input-placeholder {
                /* IE 10+ */
                color: #fff;
            }
            input:focus:-moz-placeholder {
                /* Firefox 18- */
                color: #fff;
            }
        }
        .fbt {
            background: linear-gradient(to right, #f7941d, #f9ce81);
            font-weight: 600;
            border: none;
            color: #000;
            display: inline-block;
            cursor: pointer;
            margin: 0 auto;
            border-radius: 24px;
            font-size: 14px;
            padding: 8px 20px;
            text-transform: uppercase;
            @extend %font1;
            @media #{$r1024} {
                font-size: 16px;
            }
        }
    }
}

